import React, { Component } from "react";

class Entry extends Component {
  constructor(props) {
    super(props);
    this.state = {};
  }
  changeAttendance = e => {
    const { employee, toggleActiveStatus } = this.props;
    toggleActiveStatus(employee);
  };
  changeReason = e => {
    const { employee, addReason } = this.props;
    addReason(employee, e.target.value);
  };
  render() {
    const { employee } = this.props;
    return (
      <tr>
        <td>{employee.firstName}</td>
        <td>{employee.lastName}</td>
        <td>
          {employee.department}
        </td>
        <td>
          {employee.company}
        </td>
        <td>
        <select
            value={employee.isActive}
            onChange={this.changeAttendance}
            className={`form-control ${employee.isActive ? "text-success":"text-danger"}`}
          >
            <option className="text-success" value={true}>Active</option>
            <option className="text-danger" value={false}>Disabled</option>
          </select>
        </td>
      </tr>
    );
  }
}

export default Entry;



