import React, { Component } from "react";
import MainContext from "../context";
import Employee from "./Employee";
import EmployeeForm from './EmployeeForm'


class RegisterForm extends Component {
  constructor(props) {
    super(props);
    this.state = { loading: false, authenticated: true, modal : false };
  }
  toggle =() => {
    this.setState({
      modal: !this.state.modal
    });
  }
  
  render() {
    const { loading, authenticated } = this.state;
    return (
      <MainContext.Consumer>
        {({ employees, toggleActiveStatus, handleEmployeeForm }) => (
          <React.Fragment>
              <div className="container">
                <div className="row justify-content-center">
                  <div className="col-md-10">
                  <h4 className="text-center text-dark mt-3 d-flex justify-content-between">
                List of Employees <br />
                <button className="btn py-1 btn-info pull-right" onClick={this.toggle}>add</button>

                </h4>
                <div className="table-responsive">
                <table className="table table-bordered table-striped mt-4">
                  <thead className="">
                    <tr>
                      <td>
                        <b>Όνομα</b>
                      </td>
                      <td>
                        <b>Επίθετο</b>
                      </td>
                      <td>
                        <b>Department</b>
                      </td>
                      <td>
                        <b>Company</b>
                      </td>
                      <td>
                        <b>Employment Status</b>
                      </td>
                    </tr>
                  </thead>
                  <tbody>
                    {employees.map((emp, idx) =>  (
                      <Employee
                        toggleActiveStatus={toggleActiveStatus}
                        key={idx + emp.firstName}
                        employee={emp}
                      />
                    ) )}
                  </tbody>
                </table>
                </div>
                <br />
                  </div>
                </div>
              </div>
              <div className="row mt4 justify-content-center mb-5">
                <div className="col-sm-8 col-md-5">
                        <EmployeeForm className="" toggle={this.toggle} modal={this.state.modal} handleSubmit={ (data,error) => {handleEmployeeForm(data,error); this.toggle()} } />
                </div>
              </div>
          </React.Fragment>
        )}
      </MainContext.Consumer>
    );
  }
}

export default RegisterForm;



