import React, { Component } from "react";
import MainContext from "../context";
import axios from "axios"
import querystring from 'query-string';
import Entry from "./Entry";
import moment from "moment"
// import { toast as Toastify} from "react-toastify";

const LoadingView = ({ loading, authenticated }) => {
  console.log(loading, authenticated);
  return (
    <div
      style={{
        display: "flex",
        flexDirection: "column",
        alignItems: "center",
        justifyContent: "center",
        height: "100vh"
      }}
    >
      <h4 className="text-muted">
        {loading
          ? "Έλεγχος ταυτότητας "
          : !loading && !authenticated
          ? "Λυπούμαστε αλλά ο υπερσύνδεσμος έχει λήξει :("
          : "Η ταυτότητα σας εξακριβώθηκε  :)"}
      </h4>
    </div>
  );
};

class RegisterForm extends Component {
  constructor(props) {
    super(props);
    this.state = { loading: true, authenticated: false, sending:false };
  }
  componentDidMount() {
     const  values = querystring.parse(this.props.location.search)
     console.log("token:",values);
    axios
      .get(`${process.env.NODE_ENV=="production"? '':'http://localhost:7000'}/api/tokens/verify-token/`+values.token)
      .then(response => {
        console.log(response)
         this.setState({authenticated: response.data.isValid,loading : false})
      })
      .catch(ex => console.log("Failed to authenicate link", ex));
  }
  todayDate = () => {
    moment.locale('gr');
    return moment().format("dddd, MMMM Do YYYY");
  }

  render() {
    const { loading, authenticated } = this.state;
    return (
      <MainContext.Consumer>
        {({ employees, toggleAttendance, addReason, submitForm, sending }) => (
          <React.Fragment>
            {loading ? (
              <LoadingView {...this.state} />
            ) : !loading && !authenticated ? (
              <LoadingView {...this.state} />
            ) : (
              <div className="container">
                <div className="row justify-content-center">
                  <div className="col-10">
                  <h3 className="text-center text-dark mt-3">
                Ημερήσιο Παρουσιολόγιο Εργαζομένων <br />
                </h3>
                <small className="">{this.todayDate()}</small>
                <div className="table-responsive">
                <table className="table table-bordered table-striped responsive mt-2">
                  <thead className="">
                    <tr>
                      <td>
                        <b>Όνομα</b>
                      </td>
                      <td>
                        <b>Επίθετο</b>
                      </td>
                      <td>
                        <b>Παρουσία</b>
                      </td>
                      <td>
                        <b>Λόγος Απουσίας</b>
                      </td>
                    </tr>
                  </thead>
                  <tbody>
                    {employees.map((emp, idx) => emp.isActive ? (
                      <Entry
                        addReason={addReason}
                        toggleAttendance={toggleAttendance}
                        key={idx + emp.firstName}
                        employee={emp}
                      />
                    ): null)}
                  </tbody>
                </table>
                </div>
                <small className="text-muted">
                Σημείωση: Προσοχή στην εισαγωγή των δεδομένων εάν συμπληρώσετε τα στοιχεία και πατήσετε υποβολή δεν θα είστε σε θέση να τροποποιήσετε τα στοιχεία της υποβολής σας.
                </small>{" "}
                <br />
                <button onClick={sending?null:() => {
                  submitForm( function(error){
                    if(error)return window.alert("Error: failed to submit register")
                    window.alert("Success: Attendance register submitted successfully")
                  })
                
                }} className="btn mt-3 btn-primary">
                {sending? '......':"Υποβολή "}
                </button>
                  </div>
                </div>
              </div>
            )}
          </React.Fragment>
        )}
      </MainContext.Consumer>
    );
  }
}

export default RegisterForm;



