import React, { Component } from "react";
import { Route, Switch, Redirect } from "react-router-dom";
import RegisterForm from "./RegisterForm";
import RegisterSubmited from "./RegisterSubmited";
import EmployeeList from './EmployeeList';
import MainContext from "../context";
import axios from "axios";
import {NavLink} from "react-router-dom"

// import axios from "axios";

class Router extends Component {
  constructor(props) {
    super(props);
    this.state = {
      employees: [],
      sending:false
    };
  }
  componentDidMount() {
    this.getEmployees()
  }
  getEmployees = ( ) => {
    axios
      .get(`${process.env.NODE_ENV=="production"? 'https://presence.kolleris.com':'http://localhost:7000'}/api/employees/all`)
      .then(resp => {
        console.log("emp:",resp.data)
    let employees = resp.data.map(emp => { return {...emp, attended:true}})
    console.log(employees);
        this.setState({ employees });
      })
      .catch(ex => console.log("Failed to fetch employee", ex));
  }
  toggleAttendance = employee => {
    const updatedEmployees = this.state.employees.map(emp => {
      if (emp._id !== employee._id) return emp;
      return { ...emp, attended: !employee.attended, reason: "" };
    });
    this.setState({ employees: updatedEmployees });
  };
  
  toggleActiveStatus = employee => {
    const updateData = {isActive : !employee.isActive}
    console.log(updateData);
    axios
        .put(`${process.env.NODE_ENV=="production"? '':'http://localhost:7000'}/api/employees/update/${employee._id}`,updateData)
        .then(employees => {
      console.log(employees);
    const updatedEmployees = this.state.employees.map(emp => {
      if (emp._id !== employee._id) return emp;
      return { ...emp, isActive: !employee.isActive };
    });
    this.setState({ employees: updatedEmployees });
        })
        .catch(ex => console.log("Failed to fetch employee", ex));


  };
  addReason = (employee, reason) => {
    const updatedEmployees = this.state.employees.map(emp => {
      if (emp._id !== employee._id) return emp;
      return { ...emp, reason };
    });
    this.setState({ employees: updatedEmployees });
  };
  submitForm = (cb) => {
      const { employees } = this.state;
    this.setState({sending : true})
    const currentDate = Date.now();
    const attendanceList = employees.map(emp => ({
      date: currentDate,
      employee: emp._id,
      reason: emp.reason,
      attended: emp.attended
    }));
    console.log("here");
    axios
      .post(`${process.env.NODE_ENV=="production"? '':'http://localhost:7000'}/api/attendance/create`, attendanceList)
      .then(result => {
        this.setState({sending:"false"})
        if(result.status == 200) return cb(false)
        this.setState({sending:"false"})
        console.log("sent");
        // this.props.history.push("/register-submited");
        return cb(true)
        
      })
      .catch(err => {
        console.log(err);
         return cb(true)
      });
  };
  handleEmployeeForm = (data,cb) => {
       axios.post(`${process.env.NODE_ENV=="production"? '':'http://localhost:7000'}/api/employees/create`,data).then((emp) => {
         this.getEmployees()
         return cb(false) 
       }).catch((err) => {
         console.log(err);
         return cb(true)
       });
  }
  render() {
    const { employees } = this.state;
    return (
      <React.Fragment>
        <MainContext.Provider
          value={{
            employees,
            toggleAttendance: this.toggleAttendance,
            toggleActiveStatus : this.toggleActiveStatus,
            addReason: this.addReason,
            submitForm: this.submitForm,
            handleEmployeeForm : this.handleEmployeeForm
          }}
        >
          <nav className="bg-light navbar" style={{ width: "100%" }}>
            <div className="row ">
              <div className="col-3 ml-6 mr-auto text-dark">
                <h5 className="text-dark"><strong>Presence</strong></h5>
              </div>
              <div className="col-6 nav">
              </div>
            </div>
            <NavLink className="pull-right nav-link" to="/employees/hi435w89whw46" >Manage Employees</NavLink>

          </nav>
          <section className="mb-5 container-fluid" style={{minHeight:"70vh"}}>
            <Switch>
              <Route exact path="/" component={RegisterForm} />
              <Route
                exact
                path="/employees/hi435w89whw46"
                component={EmployeeList}
              />
              <Route
                exact
                path="/register-submited"
                component={RegisterSubmited}
              />
            </Switch>
          </section>
        </MainContext.Provider>
        <footer className="nav navbar py-3 mt-5" style={{backgroundColor:'#333'}}>
         <a href="https://kolleris.com">kolleris.com</a>
      </footer>
      </React.Fragment>
      
    );
  }
}

export default Router;



