import React, { Component } from "react";
// import {toast as Toastify} from "react-toastify";
import {Button, Modal, ModalBody, ModalFooter, ModalHeader } from "reactstrap";

class EmployeeForm extends Component {
  constructor(props) {
    super(props);
    this.state = {
      firstName: "",
      lastName: "",
      department: "",
      phone: "",
      company: "",
      sending: ""
    };
  }
  handleChange = e => {
    this.setState({ [e.target.name]: e.target.value });
  };
  handleSubmit = e => {
    e.preventDefault();
    const { firstName, lastName, phone, department, company } = this.state;
    if (this.state.sending) return;
    this.props.handleSubmit(
      { firstName, lastName, phone, department, company },
      error => {
        if (error) return window.alert("Error: Failed to save employee");
        window.alert("Success : Employee saved successfully");
      }
    );
    this.setState({
      firstName: "",
      lastName: "",
      department: "",
      company: "",
      phone: "",
      sending: false
    });
  };
  render() {
    const {
      firstName,
      lastName,
      department,
      company,
      sending,
      phone
    } = this.state;
    const {className, modal, toggle } = this.props;
    return (
      <div>
        <Modal
          isOpen={modal}
          toggle={toggle}
          className={className}
        >
          <form onSubmit={this.handleSubmit} className="form mb-5">
            <ModalHeader toggle={this.toggle}>New Employee</ModalHeader>
            <ModalBody>
              <div className="form-group">
                <label htmlFor="firstName" className="form-group-label">
                  First Name{" "}
                </label>
                <input
                  name="firstName"
                  value={firstName}
                  onChange={this.handleChange}
                  type="text"
                  className="form-control"
                />
              </div>
              <div className="form-group">
                <label htmlFor="lastName" className="form-group-label">
                  Last Name{" "}
                </label>
                <input
                  name="lastName"
                  value={lastName}
                  onChange={this.handleChange}
                  type="text"
                  className="form-control"
                />
              </div>
              <div className="form-group">
                <label htmlFor="phone" className="form-group-label">
                  Phone{" "}
                </label>
                <input
                  name="phone"
                  value={phone}
                  onChange={this.handleChange}
                  type="text"
                  className="form-control"
                />
              </div>
              <div className="form-group">
                <label htmlFor="department" className="form-group-label">
                  {" "}
                  Department{" "}
                </label>
                <input
                  name="department"
                  value={department}
                  onChange={this.handleChange}
                  type="text"
                  className="form-control"
                />
              </div>
              <div className="form-group">
                <label htmlFor="company" className="form-group-label">
                  {" "}
                  Company{" "}
                </label>
                <input
                  name="company"
                  value={company}
                  onChange={this.handleChange}
                  type="text"
                  className="form-control"
                />
              </div>
            </ModalBody>
            <ModalFooter>
            <input
                  type="submit"
                  value={sending ? "...." : "Submit"}
                  className="btn btn-info"
                />
              {" "}
              <Button color="secondary" onClick={toggle}>
                Cancel
              </Button>
            </ModalFooter>
          </form>
        </Modal>
      </div>
    );
  }
}

export default EmployeeForm;



