import React, { Component } from "react";

class Entry extends Component {
  constructor(props) {
    super(props);
    this.state = {};
  }
  changeAttendance = e => {
    const { employee, toggleAttendance } = this.props;
    toggleAttendance(employee);
  };
  changeReason = e => {
    const { employee, addReason } = this.props;
    addReason(employee, e.target.value);
  };
  render() {
    const { employee } = this.props;
    return (
      <tr>
        <td>{employee.firstName}</td>
        <td>{employee.lastName}</td>
        <td>
          <select
            value={employee.attended}
            onChange={this.changeAttendance}
            className="form-control"
          >
            <option value={true}>Yes</option>
            <option value={false}>No</option>
          </select>
        </td>
        <td>
          {!employee.attended ? (
            <select
              value={employee.reason}
              onChange={this.changeReason}
              className="form-control"
            >
              <option value="Ασθένεια με χαρτί γιατρού">
                Ασθένεια με χαρτί γιατρού
              </option>
              <option value="Κανονική άδεια">Κανονική άδεια</option>
              <option value="Απουσία">Απουσία</option>
            </select>
          ) : null}
        </td>
      </tr>
    );
  }
}

export default Entry;



